@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: rgba(248, 248, 248, 1);
    font-family: 'Nunito', sans-serif;
}

.btn{
    width: 100px;
    height: 34px;
    border-radius: 80px;
    border: none;
    background-color: rgba(244, 224, 65, 1);
    cursor: pointer;
    transition: all 0.1s;
}



.container{
    max-width: 1024px;
    margin: 0 auto;

}

.active{
    border: 1px solid red;
    background: red;
    color: aquamarine;
}

@media (any-hover: hover) {
    .btn:hover{
        background: rgba(255, 227, 2, 1);
    }
}   

.btn:active{
    background: rgba(180, 180, 180, 1);
    color: #fff;
}

@media (max-width: 1024px) {
    .container{
        max-width: 1024px;
        padding: 0 60px;
    }
}

@media (max-width: 768px) {
    .container{
        max-width: 768px;
        padding: 0 32px;
    }
}

@media (max-width: 576px) {
    .container{
        max-width: 576px;
        padding: 0 16px;
    }
}

@media (max-width: 360px) {
    body{
        width: 360px;
    }
}

