.about-us{
    max-width: 1070px;
    margin: 0 auto;
    position: relative;
    background: url('../../img/pexels-alexandr-podvalny-1227513.jpeg') no-repeat;
    background-size: cover;
    background-position: center;
    color: #fff;
}

.about-us__shadow{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
}

.about-us__content{
    max-width: 380px;
    line-height: 26px;
    text-align: center;
    padding: 164px 0;
    margin: 0 auto;
    position: relative;
}

.about-us__title{
    font-size: 40px;
    line-height: 40px;
}

.about-us__text{
    margin: 21px 0 32px 0;
}

@media (max-width: 768px) {
    .about-us__content{
        padding: 88px 0;
    }
}

@media (max-width: 576px) {
    .about-us__content{
        padding: 40px 0;
    }
}

@media (max-width: 360px) {
    .about-us{
        width: 360px;
    }
}