.users{
    text-align: center;
}

.users__title{
    margin: 140px 0 50px 0;
    font-size: 40px;
}

.users__wrapper{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(322px, 1fr));
    gap: 29px;
}

.users__item{
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    line-height: 26px;
}

.item__img-block{
    margin: 0 auto;
    margin-bottom: 20px;
}

.item__img-block img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
}

.item__name{
    margin-bottom: 20px;
}

.item__email{
    cursor: pointer;
}

.item__phone{
    text-decoration: none;
    color: #000;
}

.users__btn{
    margin: 50px auto;
}

.users__loader{
    width: 100px;
    margin: 0 auto;
    animation: loaded 0.8s linear infinite;
}

@keyframes loaded {
    to {
        transform: rotate(360deg);
    }
}

@media (max-width: 768px) {
    .users__wrapper{
        gap: 16px;
    }
}