.form-block{
    padding-bottom: 100px;
}

.form-block__title{
    text-align: center;
    font-size: 40px;
    margin: 60px 0 50px 0;
}

.form__item{
    max-width: 380px;
    height: 54px;
    margin: 0 auto;
    margin-bottom: 50px;
    border: 1px solid rgba(208, 207, 207, 1);
    border-radius: 4px;
    position: relative;
    color: rgba(126, 126, 126, 1);
}

.form__input.error{
    border: 1px solid red;
}

.form__file.error{
    border: 1px solid red;
}

.error-text{
    color: red;
}

.error-position{
    text-align: end;
}

.form__input{
    width: 100%;
    height: 100%;
    padding: 0 14px;
    background-color: transparent;
    border: none;
    outline: none;
    border-radius: 4px;
}

.number::before{
    content: "+38 (XXX) XXX - XX - XX";
    display: block;
    position: absolute;
    bottom: -16px;
    left: 14px;
    font-size: 12px;
}

.form__item label {
    position: absolute;
    top: calc(50% - 8px);
    left: 0;
    display: block;
    transition: all 0.2s;
}

.form__input:focus ~ label,
.form__input:not(:placeholder-shown) ~ label  {
    top: -8px;
    left: 10px;
    font-size: 12px;
    padding: 0 3px;
    background-color: rgba(248, 248, 248, 1);
}

.block-position__item{
    margin-bottom: 7px;
}

.block-position__input{
    display: none;
}

.block-position{
    max-width: 380px;
    margin: 0 auto;
    margin-bottom: 47px;
}

.block-position__text{
    margin-bottom: 20px;
}

label{
    padding-left: 30px;
    position: relative;
    cursor: pointer;
}

.block-position__input ~ label::before{
    content: '';
    height: 20px;
    width: 20px;
    border-radius: 50%;
    border: 1px solid #9c9c9c;
    position: absolute;
    left: 0;
    cursor: pointer;
    transition: all 0.3s;
}

.block-position__input:checked ~ label::before {
    border: 1px solid #00BDD3;
}

.block-position__input ~ label::after {
    content: '';
    width: 10px;
    height: 10px;
    position: absolute;
    top: 6px;
    left: 6px;
    background: transparent;
    border-radius: 50%;
    transition: all 0.3s;
}

.block-position__input:checked ~ label::after {
    background: #00BDD3;
}

.form__file.error{
    width: 100%;
    border-radius: 4px;
    border: 1px solid red;
}

.form__file::file-selector-button{
    width: 120px;
    height: 52px;
    margin-right: 16px;
    border: none;
    background: transparent;
    border: 1px solid;
    cursor: pointer;
}

.form__file.error::file-selector-button{
    border-right: 1px solid red;
}

.form__btn{
    display: block;
    margin: 0 auto;
}

@media (max-width: 576px) {
    .form__item, .block-position{
        width: 328px;
    }
}